import React, { Fragment, Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { updateTitle } from "../../common/UpdateTitle";
import axiosInstance from "../../../actions/axios";
import { bkashCreate, bkashExecute } from "../../../actions/payment";
import store from "../../../store";
import { Button, Modal } from "antd";

class QbankDetails extends Component {
  state = {
    qbank: {},
    paymentModal: true,
  };

  showModal = () => {
    if (this.state.qbank.price === 0) {
      console.log("Price is Zero");
    } else {
      this.setState({ paymentModal: true });
    }
  };
  handleCancel = () => {
    this.setState({ paymentModal: false });
  };

  bkash_function = () => {
    //call the bkash init function

    //eslint-disable-next-line
    bKash.init({
      //options -
      //1) 'checkout' : Performs a single checkout.
      paymentMode: "checkout",

      paymentRequest: {
        amount: store.getState().payment.price,
        intent: "sale",
      },

      createRequest: function (request) {
        let body = {
          amount: store.getState().payment.price,
          userID: store.getState().auth.user.id,
        };
        console.log(store.getState().payment, store.getState().auth.user.id);
        axiosInstance
          .post("/payment/bkash/create", JSON.stringify(body))
          .then((res) => {
            console.log(res);
            bkashCreate(res.data.paymentID, res.data.id_token);

            //eslint-disable-next-line
            bKash.create().onSuccess(res.data);
          })
          .catch((err) => {
            Modal.error({
              title: "Failed",
              content: (
                <Fragment>
                  <p>Payment Failed!!!</p>
                </Fragment>
              ),
            });

            //eslint-disable-next-line
            bKash.create().onError();
          });
      },
      executeRequestOnAuthorization: function () {
        console.log("Execution Called");
        const { paymentID, id_token, productID } = store.getState().payment;
        let execBody = {
          Authorization: id_token,
          paymentID: paymentID,
          userID: store.getState().auth.user.id,
          qbank: productID,
        };
        axiosInstance
          .post("/payment/bkash/execute", JSON.stringify(execBody))
          .then(({ data }) => {
            if (data && data.paymentID != null) {
              console.log("Payment Succesfull!!!");
              Modal.success({
                title: "Success",
                content: (
                  <Fragment>
                    <p>Payment Succesfull!!</p>
                  </Fragment>
                ),
              });
              // On success, perform your desired action

              setTimeout(() => {
                window.location.reload();
              }, 9000);
            } else if (data.message) {
              Modal.error({
                title: "Failed",
                content: (
                  <Fragment>
                    <p>{data.message}</p>
                  </Fragment>
                ),
              });
            } else {
              console.log("bkash error");
              Modal.error({
                title: "Failed",
                content: (
                  <Fragment>
                    <p>Payment Failed!!</p>
                  </Fragment>
                ),
              });
              //eslint-disable-next-line
              bKash.execute().onError(); //run clean up code
            }
          })
          .catch((err) => {
            Modal.error({
              title: "Unsuccess!!!",
              content: (
                <Fragment>
                  <p>Payment Unsuccess!!!</p>
                </Fragment>
              ),
            });
            console.log("site error");
            //eslint-disable-next-line
            bKash.create().onError();
          });
      },
      onClose: function () {
        //define what happens if the user closes the pop up window
        Modal.error({
          title: "Cancelled",
          content: (
            <Fragment>
              <p>Payment Cancelled!!</p>
            </Fragment>
          ),
        });
      },
    });
    document.getElementById("bKash_button").removeAttribute("disabled");
  };

  componentDidMount() {
    axiosInstance
      .get(`/qbank/question-bank/${this.props.match.params.qbank_name}/`)
      .then((res) => {
        this.props.bkashExecute(res.data.id, res.data.price);
        this.setState({ ...this.state, qbank: res.data });
      })
      .catch((err) => {
        this.setState({ ...this.state, qbank: { name: "Error 404" } });
      });
    updateTitle({
      title: `${this.props.match.params.qbank_name} - Question Bank`,
    });
    this.setState({ paymentModal: false });
  }
  componentDidUpdate(prevProps, prevState) {
    // console.log("ComponentDidUpdate");
    if (prevState.qbank !== this.state.qbank) {
      if (store.getState().payment.price !== 0) {
        console.log("First Qbank and script");

        console.log("bkash called!!!!!111111");

        this.bkash_function();
      }
    }
  }

  componentWillUnmount() {
    this.props.bkashCreate(null, null);
    this.props.bkashExecute(null, 0);
    Modal.destroyAll();
    // const bKash_Script = document.getElementById("bKash_Script");
    // if (bKash_Script) {
    //   document.body.removeChild(bKash_Script);
    // }
  }

  render() {
    const { qbank } = this.state;

    return (
      <Fragment>
        <div
          className="mdk-box bg-primary mdk-box--bg-primary js-mdk-box mb-0 mt-32pt"
          data-effects="blend-background"
        >
          <div className="mdk-box__content">
            <div className="hero py-64pt text-center text-sm-left">
              <div className="container page__container">
                <div className="d-flex flex-column flex-sm-row">
                  <div className="order-1 order-sm-0">
                    <h1 className="text-white">{qbank.name}</h1>
                    <p className="lead text-white-50 measure-hero-lead mb-24pt">
                      {qbank.description}
                    </p>
                    <button
                      // id="bKash_button"
                      onClick={this.showModal}
                      className="btn btn-outline-white"
                    >
                      Enroll
                    </button>
                  </div>
                </div>
              </div>
            </div>
            <div
              className="navbar navbar-expand-sm navbar-light bg-white border-bottom-2 navbar-list p-0 m-0 align-items-center"
              style={{ position: "unset" }}
            >
              <div className="container " style={{ position: "unset" }}>
                <ul
                  className="nav navbar-nav flex align-items-sm-center"
                  style={{ position: "unset" }}
                >
                  <li className="nav-item navbar-list__item">
                    <span className="material-icons icon--left">list</span>
                    <strong>
                      {qbank.questionset ? qbank.questionset.length : 0}{" "}
                      Question Set
                    </strong>
                  </li>
                  <li className="nav-item navbar-list__item">
                    <span className="material-icons icon--left">local_atm</span>
                    <strong>{qbank.price} BDT</strong>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>

        <div className="mt-3 border-bottom-2" style={{ position: "unset" }}>
          <div className="container">
            <div className="" style={{ position: "unset" }}>
              <div className="">
                <h5>
                  <span className="material-icons icon--left">expand_more</span>
                  Subjects
                </h5>
                <ul className="list-unstyled ml-3">
                  {qbank.subject ? (
                    qbank.subject.map((sub) => (
                      <li key={sub.id} className="d-flex align-items-center">
                        <h6 className="">
                          <span className="material-icons text-50 mr-8pt">
                            check
                          </span>
                          {sub.name}
                        </h6>
                      </li>
                    ))
                  ) : (
                    <li className="d-flex align-items-center">
                      <span className="material-icons text-50 mr-8pt">
                        check
                      </span>
                      <span className="text-70">Empty</span>
                    </li>
                  )}
                </ul>
              </div>
            </div>
          </div>
        </div>

        <div className="container bg-white" style={{ position: "unset" }}>
          {qbank.questionset ? (
            qbank.questionset.map((set, index) => (
              <div
                key={set.id}
                className="border-left-2 page-section pl-32pt pb-8pt"
              >
                {index === 0 ? null : <hr className="mt-0 pt-0" />}

                <div className="d-flex align-items-center page-num-container">
                  <div className="page-num">{index + 1}</div>
                  <h4>{set.name}</h4>
                </div>
                <p className="text-70 mb-3">{set.description}</p>

                <p className="h5">
                  Topic:
                  {set.topic.map((topic, inx) => (
                    <strong key={topic.id} className="text-70">
                      {inx === 0 ? " " : ", "} {inx + 1}. {topic.name}
                    </strong>
                  ))}
                </p>
                <Link className="btn btn-primary" to="#">
                  Give Exam
                </Link>
              </div>
            ))
          ) : (
            <p></p>
          )}
        </div>

        <Modal
          footer={[
            <Button className="text-50" key="back" onClick={this.handleCancel}>
              Close
            </Button>,
          ]}
          centered={true}
          closable={false}
          visible={this.state.paymentModal}
        >
          <div>
            <button id="bKash_button" disabled className="btn btn-primary">
              Pay With bKash
            </button>
          </div>
        </Modal>
      </Fragment>
    );
  }
}
const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
  user: state.auth.user,
  payment: state.payment,
});

export default connect(mapStateToProps, { bkashCreate, bkashExecute })(
  QbankDetails
);
