import { BKASH_CREATE_REQ, BKASH_EXECUTE_REQ } from "./types";

// Create Payment Request
export const bkashCreate = (paymentID, id_token) => (dispatch) => {
  const bkashInfo = {
    paymentID: paymentID,
    id_token: id_token,
  };
  dispatch({
    type: BKASH_CREATE_REQ,
    payload: bkashInfo,
  });
};

export const bkashExecute = (productID, price) => (dispatch) => {
  const bkashInfoexec = {
    productID: productID,
    price: price,
  };
  dispatch({
    type: BKASH_EXECUTE_REQ,
    payload: bkashInfoexec,
  });
};
